import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Tabs,
  Input,
  Radio,
  Switch,
  Select,
  Tree,
  InputNumber,
  Divider,
  Form,
  message,
  Modal,
} from "antd";
import { v4 as uuid } from "uuid";
import styled from "@emotion/styled";
import {
  PlusCircleOutlined,
  FormOutlined,
  DeleteOutlined,
  SettingOutlined,
  MinusCircleOutlined,
} from "@ant-design/icons";
import { SpanSlider } from "./span-slider";
import { acceptList, buttonTypes, buttonHtmlTypes } from "./form-config";

// 
const AttrSetRow = ({
  visible = true,
  label,
  type,
  target,
  propKey,
  compInfo,
  updateCompProps,
  children,
}) => {
  const slot =
    type === "input" ? (
      <Input
        value={compInfo[target][propKey]}
        onChange={(e) =>
          updateCompProps(target, {
            [propKey]: e.target.value,
          })
        }
        maxLength={255}
        allowClear
      />
    ) : (
      children
    );

  return visible ? (
    <div className="attr-item">
      <label>{label}</label>
      <div>{slot}</div>
    </div>
  ) : null;
};

export const RightPanel = ({
  compInfo,
  formConfig,
  updateCompProps,
  setFormConfig,
}) => {
  // 
  const [attrPanelType, setAttrPanelType] = useState("form-props");

  // 
  const [selectedRows, setSelectedRows] = useState([]);
  // |
  const [modalVisible, setModalVisible] = useState(0);
  const [form] = Form.useForm();

  //  
  useEffect(() => {
    if (!compInfo) return;

    setAttrPanelType("component-props");
  }, [compInfo]);

  // 
  const onOptionChange = (type, index, key, value) => {
    let options = [];
    if (type === "change") {
      options = compInfo.props.options.map((item, eq) => {
        if (eq === index) {
          return {
            ...item,
            [key]: value,
          };
        }
        return item;
      });
    } else if (type === "remove") {
      options = compInfo.props.options.filter((_, eq) => eq !== index);
    } else if (type === "add") {
      const optionsLen = compInfo.props.options.length;

      options = [
        ...compInfo.props.options,
        {
          label: `${optionsLen}`,
          value: `${optionsLen}`,
        },
      ];
    }

    updateCompProps("props", {
      options,
    });
  };

  // 
  const updateFetchProps = (key, value) => {
    updateCompProps(null, {
      fetchProps: {
        ...compInfo.fetchProps,
        [key]: value,
      },
    });
  };

  // 
  const onAddRule = () => {
    const { rules } = compInfo.formItemProps;

    updateCompProps("formItemProps", {
      rules: [
        ...rules,
        {
          reg: "",
          message: "",
        },
      ],
    });
  };
  // 
  const onRuleChange = (index, type, value) => {
    const rulesTemp = JSON.parse(JSON.stringify(compInfo.formItemProps.rules));

    updateCompProps("formItemProps", {
      rules: rulesTemp.map((rule, rindex) => {
        if (rindex === index) {
          rule[type] = value;
          if (type === "reg" && value) {
            try {
              rule.pattern = new RegExp(value);
            } catch (err) {}
          }
        }

        return rule;
      }),
    });
  };
  // 
  const setFormItemRequired = (value) => {
    //  compInfo.formItemProps.rules 
    const rulesTemp = JSON.parse(JSON.stringify(compInfo.formItemProps.rules));

    updateCompProps("formItemProps", {
      required: value,
      rules: rulesTemp.map((rule) => {
        if (rule.required !== undefined) {
          rule.required = value;
        }

        return rule;
      }),
    });
  };

  // CascadertreeData
  const getTreeData = (options) => {
    const loop = (list) =>
      list.map((item) => {
        return {
          key: item.ukey,
          title: item.label,
          isLeaf: !item.children,
          info: item,
          ...(item.children ? { children: loop(item.children) } : {}),
        };
      });

    return loop(options);
  };
  // 
  const onTreeAdd = () => {
    setModalVisible(1);
    form.setFieldsValue({
      label: "",
      value: "",
    });
  };
  // 
  const onTreeEdit = () => {
    if (selectedRows.length <= 0) return message.error("");

    setModalVisible(2);

    form.setFieldsValue({
      label: selectedRows[0].info.label,
      value: selectedRows[0].info.value,
    });
  };
  // 
  const onTreeDelete = () => {
    if (selectedRows.length <= 0) return message.error("");

    const loop = (list) =>
      list
        .filter((item) => item.ukey !== selectedRows[0].key)
        .map((item) => {
          if (item.children) {
            item.children = loop(item.children);
          }
          return item;
        });
    updateCompProps("props", {
      options: loop(compInfo.props.options),
    });
  };
  // 
  const onTreeConfirm = async () => {
    const { label, value } = await form.validateFields();

    let options = compInfo.props.options;
    if (selectedRows.length === 0) {
      options = [
        ...options,
        {
          ukey: uuid(),
          label,
          value,
        },
      ];
    } else {
      const loop = (list) =>
        list.map((item) => {
          if (item.ukey === selectedRows[0].key) {
            // 
            if (modalVisible === 2) {
              // 
              setSelectedRows(
                selectedRows.map((sitem) => {
                  sitem.title = label;
                  sitem.info.label = label;
                  sitem.info.value = value;
                  return sitem;
                })
              );

              return {
                ...item,
                label,
                value,
              };
            }

            return {
              ...item,
              children: [
                ...(item.children || []),
                {
                  ukey: uuid(),
                  label,
                  value,
                },
              ],
            };
          }
          if (item.children) {
            item.children = loop(item.children);
          }
          return item;
        });
      options = loop(compInfo.props.options);
    }
    updateCompProps("props", {
      options,
    });
    setModalVisible(0);
  };

  //  value: string[]
  const onUploadAcceptChange = (value) => {
    const oldVal = compInfo.props.accept;

    let accept = "";
    if (value.length > 0) {
      const isOldIncludAll = oldVal === "*";
      // 
      if (isOldIncludAll) {
        accept = value.filter((item) => item !== "*").join(", ");
      }
      // 
      else if (value.indexOf("*") >= 0) {
        accept = "*";
      }
      // 
      else {
        accept = value.join(", ");
      }
    }
    //  
    else {
      accept = "*";
    }
    updateCompProps("props", { accept });
  };

  const supperProps = {
    compInfo,
    updateCompProps,
  };

  return (
    <Container>
      <Tabs activeKey={attrPanelType} onChange={setAttrPanelType}>
        <Tabs.TabPane tab="" key="component-props">
          {compInfo ? (
            <div className="attr-list">
              <AttrSetRow
                label=""
                type="input"
                target="formItemProps"
                propKey="name"
                {...supperProps}
              />
              <AttrSetRow label="">
                <Input
                  value={compInfo.formItemProps.label}
                  maxLength={255}
                  onChange={(e) => {
                    const value = e.target.value;

                    if (value.length > 255) {
                      return message.error("255");
                    } else if (/[\\]|[/:*?<>|]/.test(value)) {
                      return message.error(
                        "/\\:*?<>|"
                      );
                    }
                    updateCompProps("formItemProps", {
                      label: value,
                    });
                  }}
                  placeholder=""
                  allowClear
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.placeholder !== undefined}
                label=""
              >
                <Input
                  value={compInfo.props.placeholder}
                  maxLength={255}
                  onChange={(e) =>
                    updateCompProps("props", {
                      placeholder:
                        compInfo.props.placeholder instanceof Array
                          ? e.target.value.split(",")
                          : e.target.value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.initialValue !== undefined}
                label=""
                type="input"
                target="formItemProps"
                propKey="initialValue"
                {...supperProps}
              />
              <AttrSetRow label="">
                <SpanSlider
                  value={compInfo.span}
                  onChange={(value) =>
                    updateCompProps(null, {
                      span: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow label="">
                <SpanSlider
                  value={compInfo.formItemProps.labelCol.span}
                  onChange={(value) =>
                    updateCompProps("formItemProps", {
                      labelCol: { span: value },
                      wrapperCol: { span: 24 - value },
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.style !== undefined}
                label=""
              >
                <SpanSlider
                  marks={{
                    50: "50%",
                  }}
                  min={10}
                  max={100}
                  size="small"
                  value={
                    compInfo.props.style &&
                    (compInfo.props.style.width === "initial"
                      ? 100
                      : Number.parseInt(compInfo.props.style.width))
                  }
                  onChange={(value) =>
                    updateCompProps("props", {
                      style: { width: `${value}%` },
                    })
                  }
                />
              </AttrSetRow>
              {compInfo.type === "select" ||
              compInfo.type === "cascader" ||
              compInfo.type === "radio" ||
              compInfo.type === "checkbox" ? (
                <div>
                  <Divider></Divider>
                  {compInfo.type === "select" ||
                  compInfo.type === "cascader" ? (
                    <AttrSetRow label="">
                      <Radio.Group
                        buttonStyle="solid"
                        value={compInfo.dataType}
                        onChange={(e) =>
                          updateCompProps(null, {
                            dataType: e.target.value,
                          })
                        }
                      >
                        <Radio.Button value="static"></Radio.Button>
                        <Radio.Button value="dynamic"></Radio.Button>
                      </Radio.Group>
                    </AttrSetRow>
                  ) : null}
                  {compInfo.dataType === "dynamic" ? (
                    <SelectDataSourceSetup>
                      <div className="attr-item">
                        <label></label>
                        <div>
                          <Input
                            addonBefore={
                              <Select
                                value={compInfo.fetchProps.method}
                                style={{
                                  width: "84px",
                                }}
                                options={[
                                  {
                                    label: "GET",
                                    value: "GET",
                                  },
                                  {
                                    label: "POST",
                                    value: "POST",
                                  },
                                  {
                                    label: "PUT",
                                    value: "PUT",
                                  },
                                  {
                                    label: "DELETE",
                                    value: "DELETE",
                                  },
                                ]}
                                onChange={(e) => updateFetchProps("method", e)}
                              ></Select>
                            }
                            value={compInfo.fetchProps.url}
                            maxLength={255}
                            onChange={(e) =>
                              updateFetchProps("url", e.target.value)
                            }
                          />
                        </div>
                      </div>
                      <div className="attr-item">
                        <label></label>
                        <div>
                          <Input
                            value={compInfo.fetchProps.data}
                            onChange={(e) =>
                              updateFetchProps("data", e.target.value)
                            }
                            maxLength={255}
                            placeholder="type=1&amp;page=1"
                          />
                        </div>
                      </div>
                      <div className="attr-item">
                        <label></label>
                        <div>
                          <Input
                            value={compInfo.fetchProps.dataKey}
                            maxLength={255}
                            onChange={(e) =>
                              updateFetchProps("dataKey", e.target.value)
                            }
                            placeholder="list"
                          />
                        </div>
                      </div>
                      <div className="attr-item">
                        <label></label>
                        <div>
                          <Input
                            value={compInfo.fetchProps.label}
                            maxLength={255}
                            onChange={(e) =>
                              updateFetchProps("label", e.target.value)
                            }
                          />
                        </div>
                      </div>
                      <div className="attr-item">
                        <label></label>
                        <div>
                          <Input
                            value={compInfo.fetchProps.value}
                            maxLength={255}
                            onChange={(e) =>
                              updateFetchProps("value", e.target.value)
                            }
                          />
                        </div>
                      </div>
                      {compInfo.fetchProps.children !== undefined ? (
                        <div className="attr-item">
                          <label></label>
                          <div>
                            <Input
                              value={compInfo.fetchProps.children}
                              maxLength={255}
                              onChange={(e) =>
                                updateFetchProps("children", e.target.value)
                              }
                            />
                          </div>
                        </div>
                      ) : null}
                    </SelectDataSourceSetup>
                  ) : compInfo.type === "cascader" ? (
                    <div>
                      <Tree
                        treeData={getTreeData(compInfo.props.options)}
                        selectedKeys={selectedRows.map((item) => item.key)}
                        onSelect={(_, { selectedNodes }) =>
                          setSelectedRows(selectedNodes)
                        }
                      />
                      <div className="option-add-btn tree-option-btns">
                        <div onClick={onTreeAdd}>
                          <PlusCircleOutlined />
                          <span></span>
                        </div>
                        <div onClick={onTreeEdit}>
                          <FormOutlined />
                          <span></span>
                        </div>
                        <div onClick={onTreeDelete}>
                          <DeleteOutlined />
                          <span></span>
                        </div>
                      </div>
                    </div>
                  ) : (
                    <SelectDataSourceSetup>
                      {compInfo.props.options.map((option, index) => (
                        <Row
                          key={index}
                          className="select-options"
                          gutter={[5, 5]}
                        >
                          <Col span={2}>
                            <SettingOutlined />
                          </Col>
                          <Col span={10}>
                            <Input
                              value={option.label}
                              size="default"
                              maxLength={255}
                              onChange={(e) =>
                                onOptionChange(
                                  "change",
                                  index,
                                  "label",
                                  e.target.value
                                )
                              }
                            />
                          </Col>
                          <Col span={10}>
                            <Input
                              value={option.value}
                              size="default"
                              maxLength={255}
                              onChange={(e) =>
                                onOptionChange(
                                  "change",
                                  index,
                                  "value",
                                  e.target.value
                                )
                              }
                            />
                          </Col>
                          <Col span={2}>
                            <MinusCircleOutlined
                              className="option-item-delete"
                              onClick={() => onOptionChange("remove", index)}
                            />
                          </Col>
                        </Row>
                      ))}
                      <div
                        className="option-add-btn"
                        onClick={() => onOptionChange("add")}
                      >
                        <PlusCircleOutlined />
                        <span></span>
                      </div>
                    </SelectDataSourceSetup>
                  )}
                  <Divider />
                </div>
              ) : null}
              <AttrSetRow
                visible={compInfo.props.optionType !== undefined}
                label=""
              >
                <Radio.Group
                  optionType="button"
                  buttonStyle="solid"
                  value={compInfo.props.optionType}
                  onChange={(e) =>
                    updateCompProps("props", {
                      optionType: e.target.value,
                    })
                  }
                  size="small"
                  options={[
                    { label: "", value: "default" },
                    { label: "", value: "button" },
                  ]}
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.optionType === "button"}
                label=""
              >
                <Radio.Group
                  optionType="button"
                  buttonStyle="solid"
                  value={compInfo.props.buttonStyle}
                  onChange={(e) =>
                    updateCompProps("props", {
                      buttonStyle: e.target.value,
                    })
                  }
                  size="small"
                  options={[
                    {
                      label: "",
                      value: "outline",
                    },
                    {
                      label: "",
                      value: "solid",
                    },
                  ]}
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.optionType === "button"}
                label=""
              >
                <Radio.Group
                  optionType="button"
                  buttonStyle="solid"
                  value={compInfo.props.size}
                  onChange={(e) =>
                    updateCompProps("props", {
                      size: e.target.value,
                    })
                  }
                  size="small"
                  options={[
                    { label: "", value: "large" },
                    {
                      label: "",
                      value: "default",
                    },
                    { label: "", value: "small" },
                  ]}
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.type === "switch"}
                label=""
                type="input"
                target="props"
                propKey="checkedChildren"
                {...supperProps}
              />
              <AttrSetRow
                visible={compInfo.type === "switch"}
                label=""
                type="input"
                target="props"
                propKey="unCheckedChildren"
                {...supperProps}
              />
              <AttrSetRow visible={compInfo.type === "rate"} label="">
                <InputNumber
                  value={compInfo.props.count}
                  onChange={(value) =>
                    updateCompProps("props", {
                      count: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow visible={compInfo.type === "rate"} label="">
                <Switch
                  checked={compInfo.props.allowHalf}
                  onChange={(value) =>
                    updateCompProps("props", {
                      allowHalf: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.format !== undefined}
                label=""
                type="input"
                target="props"
                propKey="format"
                {...supperProps}
              />
              <AttrSetRow
                visible={compInfo.props.showNow !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.showNow}
                  onChange={(value) =>
                    updateCompProps("props", {
                      showNow: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.type === "datePicker"}
                label=""
              >
                <Radio.Group
                  optionType="button"
                  buttonStyle="solid"
                  value={compInfo.props.picker}
                  onChange={(e) =>
                    updateCompProps("props", {
                      picker: e.target.value,
                    })
                  }
                  size="small"
                  options={[
                    { label: "", value: "date" },
                    { label: "", value: "week" },
                    { label: "", value: "month" },
                    { label: "", value: "quarter" },
                    { label: "", value: "year" },
                  ]}
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.showTime !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.showTime}
                  onChange={(value) =>
                    updateCompProps("props", {
                      showTime: value,
                      format: value ? "yyyy-MM-DD HH:mm:ss" : "yyyy-MM-DD",
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.fileLimit !== undefined}
                label=""
              >
                {compInfo.fileLimit !== undefined ? (
                  <InputNumber
                    value={compInfo.fileLimit.size}
                    addonAfter={
                      <Select
                        value={compInfo.fileLimit.symbol}
                        onChange={(value) =>
                          updateCompProps(null, {
                            fileLimit: {
                              ...compInfo.fileLimit,
                              symbol: value,
                            },
                          })
                        }
                        options={[
                          {
                            label: "KB",
                            value: "KB",
                          },
                          {
                            label: "MB",
                            value: "MB",
                          },
                          {
                            label: "GB",
                            value: "GB",
                          },
                        ]}
                        style={{ width: 80 }}
                      />
                    }
                    onChange={(value) =>
                      updateCompProps(null, {
                        fileLimit: {
                          ...compInfo.fileLimit,
                          size: value,
                        },
                      })
                    }
                  />
                ) : null}
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.listType !== undefined}
                label=""
              >
                <Radio.Group
                  optionType="button"
                  buttonStyle="solid"
                  value={compInfo.props.listType}
                  onChange={(e) =>
                    updateCompProps("props", {
                      listType: e.target.value,
                    })
                  }
                  size="small"
                  options={[
                    { label: "", value: "text" },
                    { label: "", value: "picture" },
                    {
                      label: "",
                      value: "picture-card",
                    },
                  ]}
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.type === "upload"}
                label=""
                type="input"
                target="props"
                propKey="name"
                {...supperProps}
              />
              <AttrSetRow visible={compInfo.type === "upload"} label="">
                <Select
                  mode="multiple"
                  value={
                    compInfo.props.accept
                      ? compInfo.props.accept.split(", ")
                      : []
                  }
                  options={acceptList}
                  onChange={onUploadAcceptChange}
                  style={{ width: "100%" }}
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.type === "upload"}
                label=""
                type="input"
                target="props"
                propKey="content"
                {...supperProps}
              />
              <AttrSetRow visible={compInfo.type === "upload"} label="">
                <Switch
                  checked={compInfo.props.showUploadList}
                  onChange={(value) =>
                    updateCompProps("props", {
                      showUploadList: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={
                  compInfo.props.autoSize !== undefined &&
                  compInfo.props.autoSize.minRows
                }
                label=""
              >
                <InputNumber
                  value={
                    compInfo.props.autoSize && compInfo.props.autoSize.minRows
                  }
                  onChange={(value) =>
                    updateCompProps("props", {
                      autoSize: {
                        ...compInfo.props.autoSize,
                        minRows: value,
                      },
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={
                  compInfo.props.autoSize !== undefined &&
                  compInfo.props.autoSize.minRows
                }
                label=""
              >
                <InputNumber
                  value={
                    compInfo.props.autoSize && compInfo.props.autoSize.maxRows
                  }
                  onChange={(value) =>
                    updateCompProps("props", {
                      autoSize: {
                        ...compInfo.props.autoSize,
                        maxRows: value,
                      },
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.showCount !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.showCount}
                  onChange={(value) =>
                    updateCompProps("props", {
                      showCount: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.maxLength !== undefined}
                label=""
              >
                <InputNumber
                  value={compInfo.props.maxLength}
                  onChange={(value) =>
                    updateCompProps("props", {
                      maxLength: value,
                    })
                  }
                />
              </AttrSetRow>
              {compInfo.type === "button" ? (
                <React.Fragment>
                  <AttrSetRow label="">
                    <Switch
                      checked={compInfo.props.style.width === "initial"}
                      onChange={(value) =>
                        updateCompProps("props", {
                          style: {
                            width: "initial",
                          },
                        })
                      }
                    />
                  </AttrSetRow>
                  <AttrSetRow label="">
                    <Select
                      value={compInfo.props.type}
                      options={buttonTypes}
                      onChange={(value) =>
                        updateCompProps("props", {
                          type: value,
                        })
                      }
                      style={{ width: "100%" }}
                    />
                  </AttrSetRow>
                  <AttrSetRow label="">
                    <Select
                      value={compInfo.props.htmlType}
                      options={buttonHtmlTypes}
                      onChange={(value) =>
                        updateCompProps("props", {
                          htmlType: value,
                        })
                      }
                      style={{ width: "100%" }}
                    />
                  </AttrSetRow>
                  <AttrSetRow label="">
                    <Switch
                      checked={compInfo.props.danger}
                      onChange={(value) =>
                        updateCompProps("props", {
                          danger: value,
                        })
                      }
                    />
                  </AttrSetRow>
                  <AttrSetRow
                    label=""
                    type="input"
                    target="props"
                    propKey="content"
                    {...supperProps}
                  />
                  <AttrSetRow label="">
                    <Radio.Group
                      optionType="button"
                      buttonStyle="solid"
                      value={compInfo.props.size}
                      onChange={(e) =>
                        updateCompProps("props", {
                          size: e.target.value,
                        })
                      }
                      options={[
                        { label: "", value: "large" },
                        { label: "", value: "middle" },
                        { label: "", value: "small" },
                      ]}
                    />
                  </AttrSetRow>
                </React.Fragment>
              ) : null}
              <AttrSetRow
                label=""
                visible={compInfo.type !== "blank" && compInfo.type !== "text"}
              >
                <Switch
                  checked={compInfo.showLabel}
                  onChange={(value) =>
                    updateCompProps(null, {
                      showLabel: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.allowClear !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.allowClear}
                  onChange={(value) =>
                    updateCompProps("props", {
                      allowClear: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.showSearch !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.showSearch}
                  onChange={(value) =>
                    updateCompProps("props", {
                      showSearch: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.mode !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.mode}
                  onChange={(value) =>
                    updateCompProps("props", {
                      mode: value ? "multiple" : "",
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.multiple !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.multiple}
                  onChange={(value) =>
                    updateCompProps("props", {
                      multiple: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.readOnly !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.readOnly}
                  onChange={(value) =>
                    updateCompProps("props", {
                      readOnly: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.props.disabled !== undefined}
                label=""
              >
                <Switch
                  checked={compInfo.props.disabled}
                  onChange={(value) =>
                    updateCompProps("props", {
                      disabled: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                label=""
                visible={
                  compInfo.type !== "blank" &&
                  compInfo.type !== "text" &&
                  compInfo.type !== "button"
                }
              >
                <Switch
                  checked={compInfo.formItemProps.required}
                  onChange={setFormItemRequired}
                />
              </AttrSetRow>
              <AttrSetRow visible={compInfo.type === "blank"} label="">
                <InputNumber
                  value={compInfo.props.height}
                  addonAfter="px"
                  onChange={(value) =>
                    updateCompProps("props", {
                      height: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.type === "text"}
                label=""
                type="input"
                target="props"
                propKey="content"
                {...supperProps}
              />
              <AttrSetRow visible={compInfo.type === "text"} label="">
                <InputNumber
                  value={compInfo.props.fontSize}
                  addonAfter="px"
                  onChange={(value) =>
                    updateCompProps("props", {
                      fontSize: value,
                    })
                  }
                />
              </AttrSetRow>
              <AttrSetRow
                visible={compInfo.type === "text"}
                label=""
                type="input"
                target="props"
                propKey="color"
                {...supperProps}
              />
              {compInfo.formItemProps.rules !== undefined ? (
                <React.Fragment>
                  <Divider></Divider>
                  <RulesList>
                    {compInfo.formItemProps.rules.map((rule, index) =>
                      rule.required === undefined ? (
                        <div key={index} className="rule-item">
                          <div className="attr-item">
                            <label></label>
                            <div>
                              <Input
                                value={rule.reg}
                                maxLength={255}
                                onChange={(e) =>
                                  onRuleChange(index, "reg", e.target.value)
                                }
                                placeholder="^[0-9]+$"
                              />
                            </div>
                          </div>
                          <div className="attr-item">
                            <label></label>
                            <div>
                              <Input
                                value={rule.message}
                                maxLength={255}
                                onChange={(e) =>
                                  onRuleChange(index, "message", e.target.value)
                                }
                                placeholder=""
                              />
                            </div>
                          </div>
                          <div className="rule-remove-btn">
                            <MinusCircleOutlined
                              onClick={() =>
                                updateCompProps("formItemProps", {
                                  rules: compInfo.formItemProps.rules.filter(
                                    (_, rindex) => rindex !== index
                                  ),
                                })
                              }
                            />
                          </div>
                        </div>
                      ) : null
                    )}
                    <div className="option-add-btn" onClick={onAddRule}>
                      <PlusCircleOutlined />
                      <span></span>
                    </div>
                  </RulesList>
                </React.Fragment>
              ) : null}
            </div>
          ) : (
            <div className="empty-data"></div>
          )}
        </Tabs.TabPane>
        <Tabs.TabPane tab="" key="form-props">
          <AttrSetRow label="">
            <Input
              value={formConfig.name}
              maxLength={255}
              onChange={(e) => {
                const value = e.target.value;

                if (value.length > 255) {
                  return message.error("255");
                } else if (/[\\]|[/:*?<>|]/.test(value)) {
                  return message.error(
                    "/\\:*?<>|"
                  );
                }

                setFormConfig({
                  ...formConfig,
                  name: value,
                });
              }}
              placeholder=""
              allowClear
            />
          </AttrSetRow>
          <AttrSetRow label="">
            <Radio.Group
              optionType="button"
              buttonStyle="solid"
              value={formConfig.layout}
              onChange={(e) =>
                setFormConfig({
                  ...formConfig,
                  layout: e.target.value,
                })
              }
              options={[
                { label: "", value: "horizontal" },
                { label: "", value: "vertical" },
                { label: "", value: "inline" },
              ]}
            />
          </AttrSetRow>
          <AttrSetRow label="">
            <Radio.Group
              optionType="button"
              buttonStyle="solid"
              value={formConfig.labelAlign}
              onChange={(e) =>
                setFormConfig({
                  ...formConfig,
                  labelAlign: e.target.value,
                })
              }
              options={[
                { label: "", value: "left" },
                { label: "", value: "right" },
              ]}
            />
          </AttrSetRow>
          <AttrSetRow label="">
            <Radio.Group
              optionType="button"
              buttonStyle="solid"
              value={formConfig.size}
              onChange={(e) =>
                setFormConfig({
                  ...formConfig,
                  size: e.target.value,
                })
              }
              options={[
                { label: "", value: "large" },
                { label: "", value: "default" },
                { label: "", value: "small" },
              ]}
            />
          </AttrSetRow>
        </Tabs.TabPane>
      </Tabs>
      <Modal
        visible={modalVisible !== 0}
        title={modalVisible === 1 ? "" : ""}
        onCancel={() => setModalVisible(0)}
        onConfirm={onTreeConfirm}
      >
        <Form form={form}>
          <Form.Item
            name="label"
            rules={[{ required: true, message: "" }]}
          >
            <Input placeholder="" maxLength={255} />
          </Form.Item>
          <Form.Item
            name="value"
            rules={[{ required: true, message: "" }]}
          >
            <Input placeholder="" maxLength={255} />
          </Form.Item>
        </Form>
      </Modal>
    </Container>
  );
};

const Container = styled.div`
  height: 80vh;
  border-left: 1px solid #e8e8e8;
  width: 320px;
  padding-left: 10px;
  .attr-list {
    height: calc(100vh - 340px);
    overflow-y: auto;
    overflow-x: hidden;
  }
  .ant-input-affix-wrapper .ant-input {
    height: 22px;
  }
  .attr-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    color: #606266;
    & > label {
      padding-right: 10px;
      width: 80px;
      text-align: right;
    }
    & > div {
      flex: 1;
    }
  }
  .option-add-btn {
    padding-left: 24px;
    color: #2878ff;
    cursor: pointer;
    & > span {
      margin-left: 4px;
    }
  }
  .tree-option-btns {
    & > div {
      display: inline-block;
      margin-right: 20px;
      & > span {
        margin-left: 4px;
      }
    }
  }
  .empty-data {
    color: #999;
    padding: 20px 10px;
  }
`;

