import React, { useEffect, useRef, useState } from "react";
import { Form, message } from "antd";
import { CopyOutlined, FormOutlined } from "@ant-design/icons";
import styled from "@emotion/styled";
import { v4 as uuid } from "uuid";
import { ReactSortable } from "react-sortablejs";
import {
  formConf,
  inputComponents,
  selectComponents,
  layoutComponents,
} from "./form-config";
import { RightPanel } from "./right-panel";
import { FormItemRender } from "../form-item-render";
import { FooterButtons } from "./footer-buttons";
import { CompIcon } from "./comp-icons";

// formItem
const ColSpan = 12;
// formItemlabel
const labelColSpan = 6;

const isLayoutComp = (item) =>
  item.type === "blank" || item.type === "text" || item.type === "button";

export const FormGenerator = () => {
  const [form] = Form.useForm();

  // 
  const [formItems, setFormItems] = useState([]);
  // 
  const [formConfig, setFormConfig] = useState(formConf);
  //  
  const [compInfo, setCompInfo] = useState(null);

  //  
  const formItemIndex = useRef(0);

  // 
  const componentList = [
    {
      title: "",
      components: inputComponents,
    },
    {
      title: "",
      components: selectComponents,
    },
    {
      title: "",
      components: layoutComponents,
    },
  ];

  // 
  const onItemClick = (item) => {
    formItemIndex.current++;

    // key
    const newComp = {
      ...item,
      key: uuid(),
      formItemProps: {
        ...item.formItemProps,
        name: `field${formItemIndex.current}`,
        labelCol: { span: labelColSpan },
        wrapperCol: { span: 24 - labelColSpan },
      },
      // 
      span: ColSpan,
      showLabel: isLayoutComp(item) ? undefined : true,
    };

    setFormItems([...formItems, newComp]);

    // 
    setCompInfo(newComp);
  };

  //  key
  useEffect(() => {
    const hasInitData = formItems.find((item) => !item.key);

    if (hasInitData) {
      setFormItems(
        formItems.map((item) => {
          if (!item.key) {
            formItemIndex.current++;

            return {
              ...item,
              key: uuid(),
              formItemProps: {
                ...item.formItemProps,
                name: `field${formItemIndex.current}`,
                labelCol: { span: labelColSpan },
                wrapperCol: { span: 24 - labelColSpan },
              },
              // 
              span: ColSpan,
              showLabel: isLayoutComp(item) ? undefined : true,
            };
          }

          return item;
        })
      );
    }
  }, [formItems]);

  //  key
  const onItemCopy = (item) => {
    formItemIndex.current++;

    const copyComp = {
      ...item,
      key: uuid(),
      formItemProps: {
        ...item.formItemProps,
        name: `field${formItemIndex.current}`,
      },
    };
    setFormItems([...formItems, copyComp]);
  };

  // 
  const onItemDelete = (item) => {
    setFormItems(formItems.filter((el) => el.key !== item.key));

    // 
    if (compInfo && compInfo.key === item.key) {
      setCompInfo(null);
    }
  };

  //  type: formItemProps | props | null
  const updateCompProps = (type, newProps) => {
    const getUpdateObj = (item) => {
      if (type === "formItemProps") {
        return {
          formItemProps: {
            ...item.formItemProps,
            ...newProps,
          },
        };
      } else if (type === "props") {
        return {
          props: {
            ...item.props,
            ...newProps,
          },
        };
      } else {
        return newProps;
      }
    };

    // 
    setFormItems(
      formItems.map((item) => {
        if (item.key === compInfo.key) {
          return {
            ...item,
            ...getUpdateObj(item),
          };
        }

        return item;
      })
    );

    // 
    setCompInfo({
      ...compInfo,
      ...getUpdateObj(compInfo),
    });

    //  
    if (type === "formItemProps" && newProps.initialValue !== undefined) {
      form.setFieldsValue({
        [compInfo.formItemProps.name]: newProps.initialValue,
      });
    }
  };

  //  
  const onFormSave = async () => {
    // console.log({
    //   formConfig,
    //   formItems,
    // });

    if (formConfig.name.trim() === "") return message.warning("");
    if (formItems.length === 0) return message.warning("");

    // 
    const allFormItemsName = formItems.map((item) => item.formItemProps.name);
    const allFormItemsUniq = [...new Set(allFormItemsName)];
    if (allFormItemsName.length > allFormItemsUniq.length)
      return message.warning("");

    // 
    const data = {
      formConfig,
      formItems: formItems.map((item) => ({
        ...item,
        formItemProps: {
          ...item.formItemProps,
          ...(item.formItemProps.rules
            ? {
                rules: (item.formItemProps.rules || []).filter(
                  (rule) =>
                    rule.required !== undefined ||
                    (rule.reg && rule.reg.trim() !== "" && rule.message)
                ),
              }
            : {}),
        },
      })),
    };

    const vals = await form.validateFields();
    console.log(data, vals);
  };

  return (
    <Container>
      <div className="form-generator-container">
        <ComponentList>
          {componentList.map((item, index) => (
            <div className="comp-types" key={index}>
              <div>{item.title}</div>
              <div className="components">
                <ReactSortable
                  className="ant-row"
                  group={{
                    name: "comp",
                    pull: "clone",
                    put: false,
                  }}
                  draggable=".component-item"
                  animation={150}
                  sort={false}
                  list={item.components}
                  setList={(e) => {}}
                >
                  {item.components.map((comp, cindex) => (
                    <div
                      key={cindex}
                      className="component-item"
                      onClick={() => onItemClick(comp)}
                    >
                      <CompIcon type={comp.type} />
                      <span>{comp.formItemProps.label}</span>
                    </div>
                  ))}
                </ReactSortable>
              </div>
            </div>
          ))}
        </ComponentList>
        <FormLayout>
          {formItems.length === 0 ? (
            <div className="empty-tips"></div>
          ) : null}
          <Form {...formConfig} form={form}>
            <ReactSortable
              className="ant-row drop-holder"
              group="comp"
              animation={250}
              list={formItems}
              setList={setFormItems}
            >
              {formItems.map((item, index) => (
                <FormItemRender
                  key={index}
                  formConfig={formConfig}
                  item={item}
                  activeKey={compInfo ? compInfo.key : ""}
                  onClick={() => setCompInfo(item)}
                >
                  <div
                    className="icon-btn copy"
                    onClick={(e) => {
                      e.stopPropagation();
                      onItemCopy(item);
                    }}
                  >
                    <CopyOutlined />
                  </div>
                  <div
                    className="icon-btn delete"
                    onClick={(e) => {
                      e.stopPropagation();
                      onItemDelete(item);
                    }}
                  >
                    <FormOutlined />
                  </div>
                </FormItemRender>
              ))}
            </ReactSortable>
          </Form>
        </FormLayout>
        <RightPanel
          compInfo={compInfo}
          formConfig={formConfig}
          updateCompProps={updateCompProps}
          setFormConfig={setFormConfig}
        />
      </div>
      <FooterButtons onClose={() => {}} onSave={onFormSave} />
    </Container>
  );
};

const Container = styled.div`
  .form-generator-container {
    display: flex;
    height: calc(100vh - 278px);
    position: relative;
    overflow: hidden;
    padding: 0 10px;
    border: 1px solid #e8e8e8;
  }
`;

const ComponentList = styled.div`
  width: 230px;
  border-right: 1px solid #e8e8e8;
  height: 100%;
  .comp-types {
    padding: 10px 0;
  }
  .components {
    display: flex;
    flex-wrap: wrap;
    .component-item {
      width: 100px;
      border: 1px solid #cecece;
      background-color: #fafafa;
      border-radius: 3px;
      padding: 4px 10px;
      margin: 5px 10px 5px 0;
      cursor: move;
    }
  }
`;

const FormLayout = styled.div`
  height: 80vh;
  flex: 1;
  padding: 10px 10px 0;
  position: relative;
  overflow: auto;
  .drop-holder {
    min-height: 76vh;
    align-content: flex-start;
  }
  .empty-tips {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    text-align: center;
    font-size: 16px;
    color: #ccc;
  }
  .col-item {
    position: relative;
    padding: 5px;
    .icon-btn {
      position: absolute;
      z-index: 2;
      top: -10px;
      width: 22px;
      height: 22px;
      line-height: 18px;
      text-align: center;
      border-radius: 50%;
      font-size: 12px;
      border: 1px solid #555;
      background: #fff;
      cursor: pointer;
      display: none;
      &.copy {
        right: 50px;
        border-color: #409eff;
        color: #409eff;
      }
      &.delete {
        right: 20px;
        border-color: #f56c6c;
        color: #f56c6c;
      }
    }
    &:hover .icon-btn {
      display: block;
      &.copy {
        background: #409eff;
        color: #fff;
      }
      &.delete {
        background: #f56c6c;
        color: #fff;
      }
    }
    &.active .ant-form-item {
      background: rgba(40, 120, 255, 0.1);
    }
  }
  .ant-form-item {
    padding: 6px;
    border: 1px dashed #e8e8e8;
    border-radius: 2px;
    margin-bottom: 0;
    cursor: move;
    .ant-form-item-control {
      cursor: default;
    }
    &:hover {
      background: rgba(40, 120, 255, 0.1);
    }
  }
  .component-item {
    border: 1px dashed #e8e8e8;
    background: rgba(40, 120, 255, 0.1);
    border-radius: 2px;
    padding: 0 10px;
    margin: 5px;
    display: flex;
    align-items: center;
    flex: 1;
  }
`;
